<template>
    <div class="personal-container clearfix" >
         <div class="left-side" ref="leftSide">
            <LoginRegisterSide></LoginRegisterSide>
         </div>
         <div class="right-side">
             <router-view></router-view>
         </div>
    </div>
</template>

<script>
import LoginRegisterSide from '@/components/TheLoginRegisterSide.vue'
export default {
  name: 'personCenter',
  mounted () {
    if (this.$route.path.includes('/register')) {
      this.$refs.leftSide.style.height = '640px'
    }
  },
  components: {
    LoginRegisterSide
  },
  watch: {
    '$route' (to, from) {
      if (to.path.includes('/register')) {
        this.$refs.leftSide.style.height = '640px'
      } else if (from.path.includes('/register')) {
        this.$refs.leftSide.style.height = '460px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .personal-container{
    width:900px;
    margin:0 auto;
    margin-top:90px;
    padding-bottom: 116px;
    .left-side{
      float: left;
      width:360px;
      height:460px;
    }
    .right-side{
      width:440px;
      height:460px;
      float: right;
      padding:0 50px;

    }

  }

</style>
